@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{margin:0;font-family:Roboto, sans-serif}.App{display:grid;justify-content:center;width:22vw;border:solid 2px #332c47;border-radius:10px}h1{color:#332c47}.selectedItemsList{display:flex;flex-direction:column;justify-content:space-between;width:20vw;min-height:12vh;text-transform:uppercase;font-size:20px}.itemAndButton{width:20vw;display:grid;grid-template-columns:60% 40%;margin-bottom:10px}.item{list-style-type:none;line-height:30px;padding-left:10px;background-color:#59568a;color:white;border-bottom-left-radius:10px;border-top-left-radius:10px}.itemSelected{background-color:#5dc050}.button{color:white;background-color:#332c47;border:solid 1px #332c47;border-bottom-right-radius:10px;border-top-right-radius:10px}.button--top{margin-top:10px;border-radius:10px}

